    padding-bottom: 100px;
    @include breakpoint(md) {
      // 80%
      padding-top: 80px;
      padding-bottom: 160px;
    }
    @include breakpoint(xl) {
      padding-top: 100px;
      padding-bottom: 200px;
    }
    h3 {
      font-size: 18px;
      // line-height: 1.5;
      // border-bottom: 1px solid #a45833;
      // display: inline-block;
      letter-spacing: 1.5px;
      color: $near-black;
      text-transform: uppercase;
      margin-left: 3rem;
      margin-right: 3rem;
      color: $near-black;
      &.header {
        text-align: center;
        margin-bottom: 2rem;
      }
    }
    .grid-column {
      @include grid-media($lg-grid) {
        @include grid-column(14 of 16);
        @include grid-push(1 of 16);

      }
      @include grid-media($xl-grid) {
        @include grid-column(12 of 16);
        @include grid-push(2 of 16);

      }
    }
    .case-study {
      display: flex;
      flex-wrap: wrap;
      align-items: stretch;
      height: 396px;
      @include breakpoint(701px) {
        height: 288px;
      }
      @include breakpoint(md) {
        height: 288px;
      }
      @include breakpoint(xl) {
        height: 360px;
      }
      margin-bottom: 35px;
      img {
        display: none;
      }
    }

